.TimeList{
    display: flex;
    width: 50%;

    .st{
        display: flex;
        width: 22%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .data{
            border: 1px solid white;
            border-radius: 20px;
            padding: 6px;
            font-size: 1.8vh;
            font-weight:100;
        }
        .vl{
            border-left: 1px solid white;
            height: 100%;
        }
    }

    .dr{
        padding-bottom: 10px;
        text-align: left;
        padding-left: 20px;
        font-weight: 100;
        h2{
            font-size: 3.5vh;
            font-weight: 100;
        }
        h3{
            font-size: 2.5vh;
            font-weight: 100;
        }
    }

    
}